"use client";

// src/fee.ts
import * as bitcoin from "bitcoinjs-lib";
function estimateTxSize(network, toAddress) {
  const tx = new bitcoin.Transaction();
  tx.addInput(Buffer.alloc(32, 0), 0);
  tx.ins[0].witness = [Buffer.alloc(71, 0), Buffer.alloc(33, 0)];
  tx.addOutput(bitcoin.address.toOutputScript(toAddress, network), 0);
  return tx.virtualSize();
}

// src/inscription.ts
import * as bitcoin2 from "bitcoinjs-lib";
import { InscriptionId } from "@gobob/bob-sdk/dist/ordinal-api";
var TAPROOT_ANNEX_PREFIX = 80;
var PROTOCOL_ID = Buffer.from("6f7264", "hex");
var CONTENT_TYPE_TAG = bitcoin2.opcodes.OP_1;
function getTapscript(witness) {
  const len = witness.length;
  const last = witness[len - 1];
  if (typeof last === "undefined") {
    return null;
  }
  let scriptPosFromLast = 2;
  if (len >= 2 && last[0] == TAPROOT_ANNEX_PREFIX) {
    scriptPosFromLast = 3;
  }
  if (typeof witness[len - scriptPosFromLast] === "undefined") {
    return null;
  }
  return bitcoin2.script.decompile(witness[len - scriptPosFromLast]);
}
function parseInscriptions(tx) {
  let inscriptions = [];
  for (const txInput of tx.ins) {
    const tapscript = getTapscript(txInput.witness);
    if (tapscript == null) {
      continue;
    }
    const chunks = tapscript.values();
    for (let chunk = chunks.next(); !chunk.done; chunk = chunks.next()) {
      if (chunk.value != bitcoin2.opcodes.OP_FALSE) {
        continue;
      }
      if (chunks.next().value != bitcoin2.opcodes.OP_IF) {
        continue;
      }
      const data = chunks.next().value;
      if (!Buffer.isBuffer(data) && !data.equals(PROTOCOL_ID)) {
        continue;
      }
      let tags = {};
      let body = [];
      let isBody = false;
      for (let chunk2 = chunks.next(); !chunk2.done; chunk2 = chunks.next()) {
        if (chunk2.value == bitcoin2.opcodes.OP_ENDIF) {
          inscriptions.push({ tags, body });
          break;
        } else if (chunk2.value == bitcoin2.opcodes.OP_0) {
          isBody = true;
          continue;
        }
        if (!isBody) {
          const data2 = chunks.next().value;
          if (typeof chunk2.value == "number" && Buffer.isBuffer(data2)) {
            tags[chunk2.value] = data2;
          }
        } else if (Buffer.isBuffer(chunk2.value)) {
          body.push(chunk2.value);
        }
      }
    }
  }
  return inscriptions;
}
async function getTxInscriptions(electrsClient, txid) {
  const txHex = await electrsClient.getTransactionHex(txid);
  const tx = bitcoin2.Transaction.fromHex(txHex);
  return parseInscriptions(tx);
}
function parseInscriptionId(id) {
  const [txid, index] = id.split("i");
  return {
    txid,
    index: parseInt(index, 10)
  };
}
var encoder = new TextEncoder();
function createImageInscription(image) {
  const contentType = Buffer.from(encoder.encode("image/jpg"));
  return { contentType, content: image };
}
async function getInscriptionFromId(electrsClient, inscriptionId) {
  const { txid, index } = InscriptionId.fromString(inscriptionId);
  const inscriptions = await getTxInscriptions(electrsClient, txid);
  return inscriptions[index];
}
function getContentType(inscription) {
  const data = inscription.tags[CONTENT_TYPE_TAG];
  if (Buffer.isBuffer(data)) {
    return data.toString("utf-8");
  }
  return null;
}

// src/utxo.ts
import { TESTNET_ORD_BASE_PATH } from "@gobob/bob-sdk/dist/ordinal-api";
async function findUtxoForInscriptionId(electrsClient, utxos, inscriptionId) {
  const { txid, index } = parseInscriptionId(inscriptionId);
  for (const utxo of utxos) {
    if (utxo.confirmed) {
      const res = await fetch(`${TESTNET_ORD_BASE_PATH}/output/${utxo.txid}:${utxo.vout}`, {
        headers: {
          Accept: "application/json"
        }
      });
      const inscriptionUtxo = await res.json();
      if (inscriptionUtxo.inscriptions && inscriptionUtxo.inscriptions.includes(inscriptionId)) {
        return utxo;
      }
    } else if (txid == utxo.txid) {
      const inscriptions = await getTxInscriptions(electrsClient, utxo.txid);
      if (typeof inscriptions[index] !== "undefined") {
        return utxo;
      }
    }
  }
  return void 0;
}
async function findUtxosWithoutInscriptions(electrsClient, utxos) {
  const safeUtxos = [];
  for (const utxo of utxos) {
    if (utxo.confirmed) {
      const res = await fetch(`${TESTNET_ORD_BASE_PATH}/output/${utxo.txid}:${utxo.vout}`, {
        headers: {
          Accept: "application/json"
        }
      });
      const inscriptionUtxo = await res.json();
      if (inscriptionUtxo.inscriptions.length === 0) {
        safeUtxos.push(utxo);
      }
    } else {
      const inscriptions = await getTxInscriptions(electrsClient, utxo.txid);
      if (inscriptions.length === 0) {
        safeUtxos.push(utxo);
      }
    }
  }
  return safeUtxos;
}

// src/truncate.ts
var truncateBtcAddress = (address2) => address2.slice(0, 6) + "..." + address2.slice(-6);
var truncateEthRegex = /^(0x[a-zA-Z0-9]{4})[a-zA-Z0-9]+([a-zA-Z0-9]{4})$/;
var truncateEthAddress = (address2) => {
  const match = address2.match(truncateEthRegex);
  if (!match)
    return address2;
  return `${match[1]}\u2026${match[2]}`;
};

// src/address.ts
var BTC_REGTEST_REGEX = /\b([2mn][a-km-zA-HJ-NP-Z1-9]{25,34}|bcrt1[ac-hj-np-zAC-HJ-NP-Z02-9]{11,71})\b/;
var BTC_TESTNET_REGEX = /\b([2mn][a-km-zA-HJ-NP-Z1-9]{25,34}|tb1[ac-hj-np-zAC-HJ-NP-Z02-9]{11,71})\b/;
var BTC_MAINNET_REGEX = /\b([13][a-km-zA-HJ-NP-Z1-9]{25,34}|bc1[ac-hj-np-zAC-HJ-NP-Z02-9]{11,71})\b/;
var testnetRegex = new RegExp(BTC_TESTNET_REGEX);
var regtestRegex = new RegExp(BTC_REGTEST_REGEX);
var mainnetRegex = new RegExp(BTC_MAINNET_REGEX);
var isValidBTCAddress = (network, address2) => {
  switch (network) {
    case "mainnet":
      return mainnetRegex.test(address2);
    case "testnet":
      return testnetRegex.test(address2);
    case "regtest":
      return regtestRegex.test(address2);
    default:
      throw new Error(`Invalid bitcoin network configured: ${network}. Valid values are: testnet | regtest | mainnet.`);
  }
};
export {
  createImageInscription,
  estimateTxSize,
  findUtxoForInscriptionId,
  findUtxosWithoutInscriptions,
  getContentType,
  getInscriptionFromId,
  getTapscript,
  getTxInscriptions,
  isValidBTCAddress,
  parseInscriptionId,
  parseInscriptions,
  truncateBtcAddress,
  truncateEthAddress
};
